body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

section {
  margin-top: 1.5em;
}

.primarycolor, a {
  color: #3399cc;
}

.primarycolor:hover, a:hover {
  color: #203748;
}

.clickable {
  cursor: pointer;
}

.btn:focus, .btn-primary:focus {
  outline: none;
  -webkit-box-shadow:none;
          box-shadow:none;
}

.btn-outline-primary {
  color: #3399cc;
  border-color: #3399cc;
}

.btn-outline-primary:hover {
  color: #fff;
  background-color: #3399cc;
  border-color:#3399cc;
}

.btn-outline-primary:active {
  color: #fff;
  background-color: #3399cc;
}

.btn-primary {
  background-color: #3399cc;
  border-color: #3399cc;
}

.btn-primary:hover {
  background-color: #203748;
  border-color: #203748;
}
.btn-primary:active {
  background-color: #203748;
  border-color: #203748;
}


